/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-oese-block-oese-disruptive-content-block {
	background-color: #21759b;
	color: #fff;
	padding: 2px;
}

.oese_disruptive_content_wrapper .oese_disruptive_content_background {
    background: #294179;
		padding: 15px 15px 15px 15px;
}

.oese_disruptive_content_wrapper .oese_disruptive_content_background .wp-block-columns.oese_disruptive_content_columns {
    margin: 0px;
}

.oese_disruptive_content_wrapper .oese_disruptive_content_background .block-editor-inner-blocks .wp-block-columns {
    margin: 0px;
}

.oese_disruptive_content_wrapper .oese_disruptive_content_title {
		display:block;
		width:100%;
    font-size: 24px;
		font-family: 'Merriweather', serif;
    line-height: 26px;
		font-weight: 500;
    color: #fff;
    margin: 5px 0px 15px 0px !important;
}

.oese_disruptive_content_wrapper .oese_disruptive_content_left p {
    color: #fff;
    margin: 10px 0px 10px 0px;
    font-style: normal;
}

.oese_disruptive_content_wrapper .oese_disruptive_content_left_column .has-medium-font-size {
    font-size: 24px !important;
		font-style: italic;
}

.oese_disruptive_content_wrapper .oese_disruptive_content_left_column .oese-disruptive-content-description {
		margin:0px;
		line-height: unset !important;
}

.oese_disruptive_content_wrapper .oese_disruptive_content_right_column {
    display: table;
}


.oese_disruptive_content_wrapper .oese_disruptive_content_background.front .oese_disruptive_content_right_column .oese-disruptive-content-button {
		text-align: center;
    display: table-cell;
    vertical-align: middle;
}

@media screen and (max-width: 599px) {
	.oese_disruptive_content_wrapper .oese_disruptive_content_background.front .oese_disruptive_content_right_column .oese-disruptive-content-button {
			text-align: left;
			padding-top: 20px;
	}
}

.oese_disruptive_content_wrapper .oese_disruptive_content_right_column .oese-disruptive-content-button .wp-block-button__link {
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	border-radius: 3px;
	font-size: 16px !important;
	font-family: 'Raleway', sans-serif !important;
	font-weight: 300 !important;
	font-style: normal !important;
	border: 0px !important;
	padding: 15px 10px !important;
	text-decoration: none;
}


.oese_disruptive_content_wrapper .oese_disruptive_content_left_column .has-blue-color {color:rgb(33, 117, 155) !important;}
.oese_disruptive_content_wrapper .oese_disruptive_content_left_column .has-dark-gray-color {color:rgb(68, 68, 68) !important;}
.oese_disruptive_content_wrapper .oese_disruptive_content_left_column .has-medium-gray-color {color:rgb(159, 159, 159) !important;}
.oese_disruptive_content_wrapper .oese_disruptive_content_left_column .has-light-gray-color {color:rgb(230, 230, 230) !important;}
.oese_disruptive_content_wrapper .oese_disruptive_content_left_column .has-white-color {color:rgb(255, 255, 255) !important;}
.oese_disruptive_content_wrapper .oese_disruptive_content_left_column .has-oese-color-pallete-maroon-color {color:rgb(152, 31, 51) !important;}
.oese_disruptive_content_wrapper .oese_disruptive_content_left_column .has-oese-color-pallete-green-color {color:rgb(84, 153, 68) !important;}
.oese_disruptive_content_wrapper .oese_disruptive_content_left_column .has-oese-color-pallete-black-color {color:rgb(255, 255, 255) !important;}

.oese_disruptive_content_wrapper .oese-disruptive-content-button .has-blue-background-color {background-color:rgb(33, 117, 155) !important;}
.oese_disruptive_content_wrapper .oese-disruptive-content-button .has-dark-gray-background-color {background-color:rgb(68, 68, 68) !important;}
.oese_disruptive_content_wrapper .oese-disruptive-content-button .has-medium-gray-background-color {background-color:rgb(159, 159, 159) !important;}
.oese_disruptive_content_wrapper .oese-disruptive-content-button .has-light-gray-background-color {background-color:rgb(230, 230, 230) !important;}
.oese_disruptive_content_wrapper .oese-disruptive-content-button .has-white-background-color {background-color:rgb(255, 255, 255) !important;}
.oese_disruptive_content_wrapper .oese-disruptive-content-button .has-oese-color-pallete-maroon-background-color {background-color:rgb(152, 31, 51) !important;}
.oese_disruptive_content_wrapper .oese-disruptive-content-button .has-oese-color-pallete-green-background-color {background-color:rgb(84, 153, 68) !important;}
.oese_disruptive_content_wrapper .oese-disruptive-content-button .has-oese-color-pallete-black-background-color {background-color:rgb(255, 255, 255) !important;}
